// rendertextItem
import React, { useState, useEffect } from 'react'
import { Table, Card, Badge, Button, Modal, message } from 'antd';
import { columns, columns2, columns3, } from './columns';
// import { } from './data'
import axios from '../../../axios/axios'
import './style.scss'
// let params = {
//   page: 1
// }
export default function HighTable () {
  const [dataSource, setDataSource] = useState([]);

  /**
   * 
   */
  useEffect(() => {
    getData().then((res) => {
      // console.log('res.list :', res.list);
      res.list.forEach((item, index) => {
        item['key'] = index;
      })
      setDataSource([...res.list])
    })
  }, [])
  /**
   * 
   * @param {} pagination 
   * @param {} filters 
   * @param {} sorter 
   */
  const [sortOrder, setSortOrder] = useState("");
  console.log('sortOrder :', sortOrder);
  let handleChange = (pagination, filters, sorter) => {
    setSortOrder(sorter.order)
  }

  const columns4 = [
    {
      title: 'id',
      dataIndex: 'id'
    },
    {
      title: '',
      dataIndex: 'userName'
    },
    {
      title: '',
      dataIndex: 'sex',
      render (sex) {
        return sex === 1 ? '' : ''
      }
    },
    {
      title: '',
      dataIndex: 'age'
    },
    {
      title: '',
      dataIndex: 'state',
      render (state) {
        let config = {
          '1': '',
          '2': '',
          '3': '',
          '4': '',
          '5': ''
        }
        return config[state];
      }
    },
    {
      title: '',
      dataIndex: 'interest',
      render (abc) {
        let config = {
          '1': <Badge status="success" text="" />,
          '2': <Badge status="error" text="" />,
          '3': <Badge status="default" text="" />,
          '4': <Badge status="processing" text="" />,
          '5': <Badge status="warning" text="" />
        }
        return config[abc];
      }
    },
    {
      title: '',
      dataIndex: 'birthday'
    },
    {
      title: '',
      dataIndex: 'address',
      render: (text, item) => {
        console.log('item :', item);
      }
    },
    {
      title: '',
      render: (text, item) => {
        return <Button size="small"
          onClick={() => { handleDelete(item) }}
        ></Button>
      }
    }
  ]
  let handleDelete = (item) => {
    console.log('item :', item);
    let id = item.id;
    Modal.confirm({
      title: '',
      content: '' + id,
      onOk: () => {
        message.success('');
        // this.request();
      }
    })
  }

  return (
    <>
      {/* <Spin spinning={listData.length === 0 ? true : false}>
    <Table onRow={(record, index) => { return { onClick: () => { handleClick(record, index) } } }} columns={columns2} rowSelection={{ ...rowSelection }} pagination={false} dataSource={listData} />
  </Spin> */}
      <Card className='card-wrap' title="" >
        <Table
          bordered
          columns={columns}
          dataSource={dataSource}
          pagination={true}
          scroll={{ y: 240 }}
        />
      </Card>
      <Card className='card-wrap' title="" >
        <Table
          bordered
          columns={columns2}
          dataSource={dataSource}
          pagination={true}
          scroll={{ x: 2650 }}
        />
      </Card>
      <Card className='card-wrap' title="" >
        <Table
          bordered
          columns={columns3}
          dataSource={dataSource}
          pagination={true}
          onChange={handleChange}
        />
      </Card>
      <Card className='card-wrap' title="" >
        <Table
          bordered
          columns={columns4}
          dataSource={dataSource}
          pagination={true}
        />
      </Card>
    </>
  )
}

async function getData () {
  let res = await axios.axiosGet({
    url: '/table/high/list',
  }).then((res) => {
    return res
  })
  let result = await res.result;
  return result
}



