import React, { useState } from 'react'
import { Card, Pagination, Button } from 'antd'
import './style.scss'
export default function Paginations () {
  const [cpage, setCpage] = useState(3);
  function onShowSizeChange (current, pageSize) {
    console.log(current, pageSize);
  }
  // 
  function handleOnChange (page, pageSize) {
    console.log(page, pageSize);
    setCpage(page)
  }
  // 
  function onChange (pageNumber) {
    console.log('Page: ', pageNumber);
  }
  // 
  function showTotal (total) {
    return `Total ${total} items`;
  }
  // 
  function itemRender (current, type, originalElement) {
    if (type === 'prev') {
      return <>Previous</>;
    }
    if (type === 'next') {
      return <>Next</>;
    }
    return originalElement;
  }
  return (
    <>
      <Card title="" className="card-wrap">
        <Pagination defaultCurrent={1} total={50} />
      </Card>
      <Card title="" className="card-wrap">
        <Pagination defaultCurrent={6} total={500} />
      </Card>
      <Card title="" className="card-wrap">
        <Pagination
          onChange={handleOnChange}
          showSizeChanger
          onShowSizeChange={onShowSizeChange}
          defaultCurrent={3}
          total={500}
        />
        <br />
        <Pagination
          showSizeChanger
          onShowSizeChange={onShowSizeChange}
          defaultCurrent={3}
          total={500}
          disabled
        />

      </Card>
      <Card title="" className="card-wrap">
        <Pagination showQuickJumper defaultCurrent={2} total={500} onChange={onChange} />
        <br />
        <Pagination showQuickJumper defaultCurrent={2} total={500} onChange={onChange} disabled />
      </Card>
      {/* size:small */}
      <Card title="" className="card-wrap">
        <Pagination size="small" total={50} />
        <Pagination size="small" total={50} showSizeChanger showQuickJumper />
        <Pagination size="small" total={50} showTotal={showTotal} />
      </Card>
      {/* simple */}
      <Card title="" className="card-wrap">
        <Pagination simple defaultCurrent={2} total={50} />
      </Card>
      <Card title="" className="card-wrap">
        <Pagination
          onChange={handleOnChange}
          defaultCurrent={3}
          total={50}
          current={cpage}
        />
        <Button onClick={() => { setCpage(2) }}>2</Button>
      </Card>
      <Card title="" className="card-wrap">
        <Pagination
          total={85}
          showTotal={total => `Total ${total} items`}
          pageSize={20}
          defaultCurrent={1}
        />
        <br />
        <Pagination
          total={85}
          showTotal={(total, range) => `${range[0]}-${range[1]} of ${total} items`}
          pageSize={20}
          defaultCurrent={1}
        />
      </Card>
      <Card title="" className="card-wrap">
        <Pagination total={50} itemRender={itemRender} />
      </Card>
    </>
  )
}
