import React from 'react'
import { Button, Form, Select } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
export default function FilterForm () {
  return (
    <>
      <Form layout="inline" >
        <FormItem label="" name="city_id">
          <Select
            style={{ width: 100 }}
            placeholder=""
          >
            <Option value=""></Option>
            <Option value="1"></Option>
            <Option value="2"></Option>
            <Option value="3"></Option>
          </Select>
        </FormItem>
        <FormItem label="" name="mode">
          <Select
            style={{ width: 120 }}
            placeholder=""
          >
            <Option value=""></Option>
            <Option value="1"></Option>
            <Option value="2"></Option>
          </Select>
        </FormItem>
        <FormItem label="" name="op_mode">
          <Select
            style={{ width: 80 }}
            placeholder=""
          >
            <Option value=""></Option>
            <Option value="1"></Option>
            <Option value="2"></Option>
          </Select>
        </FormItem>
        <FormItem label="" name="auth_status">
          <Select
            style={{ width: 100 }}
            placeholder=""
          >
            <Option value=""></Option>
            <Option value="1"></Option>
            <Option value="2"></Option>
          </Select>
        </FormItem>
        <FormItem>
          <Button type="primary" style={{ margin: '0 20px' }}></Button>
          <Button></Button>
        </FormItem>
      </Form>
    </>
  )
}
