import React, { useState, useEffect } from 'react'
import { Card, Button, Modal, message, } from 'antd';
import axios from '../../axios/axios';
import BaseForm from './../../components/BaseForm/BaseForm';
import ETable from './../../components/ETable/EtableFun1'
import { columns, formList, params, } from './data'
import './order.scss'
import { useHistory } from 'react-router-dom'


export default function Order () {
  const [list, setList] = useState([]);
  const [pagination, setpagination] = useState(null);
  const [item, setItem] = useState();
  let history = useHistory();
  console.log('pagination', pagination)
  useEffect(() => {
    requestList().then((res) => {
      let arrRes = res.result.item_list;
      arrRes.forEach((item, index) => {
        // id,Bug
        item['key'] = index + 1;
      })
      setList([...arrRes])
      setpagination({
        showTotal: () => {
          return `${arrRes.length}`
        }
      })
    })
  }, [])
  let openOrderDetail = () => {
    if (!item) {
      Modal.info({
        title: '',
        content: ''
      })
      return;
    }
    // window.open(`/common/order/detail/${item.id}`, '_blank');
    history.push(`/common/order/detail/${item.id}`)
  }
  let handleConfirm = () => {
    if (!item) {
      Modal.info({
        title: '',
        content: ''
      })
      return;
    }
    Modal.info({
      title: '',
      content: `{${item.order_sn}},(${item.user_name})`,
      okText: '',
      onOk: () => {
        //
        // 
        // 
        message["success"]({
          content: '',
        })
        // 
        // 
      }
    })
    console.log('item :', item);
  }
  /**
   * 
   */
  let handleFilter = (params) => {
    // this.params = params;
    console.log('params', params)
    // paramsparams,, 
  }
  return (
    <>
      <Card className="card-wrapper">
        <BaseForm
          formList={formList.formList}
          initValue={formList.initValue}
          filterSubmit={handleFilter}
        ></BaseForm>
      </Card>

      <Card className="card-wrapper" style={{ marginTop: 10 }}>
        <Button onClick={openOrderDetail}>
          
        </Button>
        <Button type="primary" style={{ marginLeft: 10 }} onClick={handleConfirm}>
          
        </Button>
      </Card>
      <Card className="card-wrapper">
        <div className="content-wrap">
          <ETable
            // rowSelection='checkbox'
            columns={columns}
            dataSource={list}
            pagination={true}
            getItem={
              rec => {
                setItem(rec)
              }
            }
          ></ETable>
        </div>
      </Card>
    </>
  )
}

const requestList = () => {
  return axios.ajax({
    url: 'http://www.qiuhang.club:7300/mock/5e8c119b00fbdf09dcf21f9f/bike/order/list',
    data: {
      params: {
        page: params.page
      }
    }
  }).then((res) => {
    return res
  })
}