import React, { useEffect, useState } from 'react'
import { Card, Descriptions, Badge, Button } from 'antd'
import './order.scss';
import axios from '../../axios/axios'
import moment from 'moment';
// ,propid???
// ---->--->proper--->,proper
// url??,
//,
//,,
//--->id-->url-->
import { useParams, useHistory } from 'react-router-dom'
const Item = Descriptions.Item;
function Detail (props) {
  const [result, setResult] = useState([])
  let { orderId } = useParams();
  let history = useHistory();
  useEffect(() => {
    axios.axiosGet({
      url: '/order/detail',
      data: {
        params: {
          orderId: orderId
        }
      }
    }).then((res) => {
      console.log('res :', res);
      if (res.code === '0') {
        setResult(res.result)
      }
    })
  }, [orderId])
  let startTime = () => {
    let a = new Date().setTime(result.start_time);
    return <>{moment(a).format('YYMMDD /  HHmmss')}</>
  }
  let endTime = () => {
    let a = new Date().setTime(result.end_time);
    return <>{moment(a).format('YYMMDD /  HHmmss')}</>
  }
  return (
    <div className="contentOrder">
      <Card title="" className='card'>
        <Button type="primary" onClick={() => { history.goBack() }}></Button>
      </Card>
      <Card title="">
        <Descriptions title='' bordered>
          <Item label='' span={12}>
            {result.order_sn}
          </Item>
          <Item label='' span={12}>
            {result.user_name}
          </Item>
          <Item label='' span={2}>
            {result.mobile}
          </Item>
          <Item label='' span={2}>
            {result.distance}
          </Item>
          <Item label='' span={2}>
            {result.total_time}
          </Item>
          <Item label='' span={2}>
            {result.status === 1 ? <Badge status="processing" text="" /> : <Badge status="success" text="" />}
          </Item>
          <Item label='' span={2}>
            {
              result.start_time ? startTime() : ''
            }
          </Item>
          <Item label='' span={2}>
            {result.start_time ? endTime() : ''}
          </Item>
          <Item label='' span={2}>
            {1000}
          </Item>
          <Item label='' span={2}>
            {300}
          </Item>
        </Descriptions>
      </Card>
    </div>
  )
}

export default Detail
