import React, { useState, useEffect, useRef } from 'react'
import { Card, Button, Modal, Col, Row } from 'antd'
import axios from '../../axios/axios'
import BaseForm from './../../components/BaseForm/BaseForm'
import EtableFun from '../../components/ETable/EtableFun1';
import './user.scss'
import { columns, formList, create, read, update, del } from './data'
import { useHistory } from 'react-router-dom'
import UpdateForm from './updateForm'
// card-wrapper
const params = {
  page: 1
}
export default function User () {
  const [list, setList] = useState([]);
  const [Item, setItem] = useState(null);
  // Model
  const [visible, setVisible] = useState(false);
  const [upvisible, setUpVisible] = useState(false);
  const [title, setTitle] = useState('')

  let updateForm = null;

  const [type, setType] = useState('create')
  let obj = {
    create, read, update, del
  }

  let history = useHistory()
  let baseForm = useRef(null)
  useEffect(() => {
    requestList().then((res) => {
      console.log('res :', res);
      let arrRes = res.result.list;
      arrRes.forEach((item) => {
        item['key'] = item.id;
      })
      setList([...arrRes])
    })
  }, [])

  let handleFilter = (params) => {
    // this.params = params;
    console.log('params', params)
    // paramsparams,, 
  }
  /**
   * 
   */
  let handleC = () => {
    setType('create')
    setVisible(true);
    setTitle('');
  }
  let onCreate = values => {
    console.log('create')
    Modal.info({
      title: '',
      content: ''
    })
    setVisible(false);
  };
  /**
   * 
   */
  let handleR = () => {
    // setVisible(true);
    // setType('read')
    // setTitle('');
    if (!Item) {
      Modal.info({
        title: '',
        content: ''
      })
      return;
    }
    history.push(`/common/user/detail/${Item.id}`)
  }
  //
  let onRead = values => {
    console.log('read')
    setVisible(false);
  };
  /**
   * 
   */
  let handleU = () => {
    // console.log('baseForm', baseForm)
    // baseForm.current.formRef.current.resetFields()
    /* 
      //formmodalmodalforcerender,
    // 
    // console.log('baseForm', baseForm)
    // baseForm.current.formRef.current.resetFields()
     */
    if (!Item) {
      Modal.info({
        title: '',
        content: ''
      })
      return;
    }
    update.initValue = {
      'username': Item.username,
      'sex': Item.sex,
      // 'date-picker': Item.birthday,
      'address': Item.address,
    }
    // updateForm && updateForm.current.resetFields()
    if (updateForm) {
      updateForm.current.setFieldsValue({
        ...update.initValue
      })
    }
    setUpVisible(true);
  }
  let onUpdate = () => {

  }

  let updataOnOk = () => {
    setItem((prev) => {
      let target = {};
      Object.assign(target, prev, updateForm.current.getFieldsValue())
      return target
    })
    // console.log('list :', list);
    let ids, target = {};
    for (let i = 0; i < list.length; i++) {
      if (list[i].id === Item.id) {
        let old = list[i];
        Object.assign(target, old, updateForm.current.getFieldsValue())
        ids = i;
        console.log(ids)
        break;
      }
    }
    if (ids > -1) {
      console.log('')
      console.log('ids :', ids);
      console.log('obj :', target);
      let newList = list;
      newList[ids] = target;
      setList([...list])
    }
    setUpVisible(false)
  }
  /**
   * 
   */
  let handleD = () => {
    if (!Item) {
      Modal.info({
        title: '',
        content: ''
      })
      return;
    }
    // history.push(`/common/user/detail/${Item.id}`)
    Modal.info({
      title: '',
      content: `${Item.username}?`,
      okText: '',
      onOk: () => {
        Modal.info({
          title: '',
          content: ``,
          onOk: () => {
            //
            requestList().then((res) => {
              console.log('res :', res);
              let arrRes = res.result.list;
              arrRes.forEach((item) => {
                item['key'] = item.id;
              })
              setList([...arrRes])
            })
          }
        })
      }
    })

  }
  // 
  let onDel = values => {
    console.log('del')
    setVisible(false);
  };
  /**
   * Modal-Modal4
   * @param {Modal-OK} type 
   * @param {} data 
   */
  let CRUD = (type, data) => {
    console.log('type :', type);
    let crud = {
      create: onCreate,
      read: onRead,
      update: onUpdate,
      del: onDel
    }
    return crud[type](data);
  }


  // .resetFields()
  return (
    <>
      <Card title="" className="card-wrapper">
        <BaseForm
          submit={formList.submit}
          formList={formList.formList}
          initValue={formList.initValue}
          filterSubmit={handleFilter}
        ></BaseForm>
      </Card>
      <Card className="card-wrapper">
        <Row>
          <Col span={2}><Button type="primary" onClick={() => handleC()}></Button></Col>
          <Col span={2}><Button type="primary" onClick={() => handleU()}></Button></Col>
          <Col span={2}><Button type="primary" onClick={() => handleR()}></Button></Col>
          <Col span={2}><Button type='danger' onClick={() => handleD()}></Button></Col>
        </Row>
      </Card>
      <Card title="" className="card-wrapper">
        <EtableFun
          columns={columns}
          dataSource={list}
          getItem={
            val => {
              console.log('Userval', val)
              setItem(val)
            }
          }
          pagination={true}
        />
      </Card>
      <Modal
        visible={visible}
        title={title}
        okText=''
        cancelText=''
        onCancel={() => setVisible(false)}
        onOk={() => {
          //???????
          //--->ref,
          let res = baseForm.current.formRef.current.getFieldsValue();
          CRUD(type, res)
        }}
      >
        <BaseForm
          formList={obj[type].formList}
          initValue={obj[type].initValue}
          formLayout={obj[type].formLayout}
          layout={obj[type].layout}
          ref={baseForm}
          filterSubmit={onCreate}
        ></BaseForm>
      </Modal>

      {/*  */}
      <Modal
        forceRender
        visible={upvisible}
        title={''}
        okText=''
        cancelText=''
        onCancel={() => setUpVisible(false)}
        onOk={() => {
          // iterator
          console.log('updateForm.current.getFieldsValue()', updateForm.current.getFieldsValue())
          updataOnOk()
        }}
      >
        <UpdateForm
          filterSubmit={(res) => {
            console.log('res', res)
            // setItem(res)
          }}
          data={update}
          reset={(formObj) => {
            console.log('formObjc', formObj)
            updateForm = formObj
          }}
        ></UpdateForm>
        {/* filterSubmit */}
      </Modal>

    </>
  )
}
const requestList = () => {
  return axios.ajax({
    url: 'http://www.qiuhang.club:7300/mock/5e8c119b00fbdf09dcf21f9f/bike/table/list',
    data: {
      params: {
        page: params.page
      }
    }
  }).then((res) => {
    return res
  })
}

