export const columns = [
  {
    title: 'id',
    key: 'id',
    width: 80,
    dataIndex: 'id'
  },
  {
    title: '',
    key: 'userName',
    width: 80,
    dataIndex: 'userName'
  },
  {
    title: '',
    key: 'sex',
    width: 80,
    dataIndex: 'sex',
    render (sex) {
      return sex === 1 ? '' : ''
    }
  },
  {
    title: '',
    key: 'state',
    width: 80,
    dataIndex: 'state',
    render (state) {
      let config = {
        '1': '',
        '2': '',
        '3': '',
        '4': '',
        '5': ''
      }
      return config[state];
    }
  },
  {
    title: '',
    key: 'interest',
    width: 80,
    dataIndex: 'interest',
    render (abc) {
      let config = {
        '1': '',
        '2': '',
        '3': '',
        '4': '',
        '5': '',
        '6': '',
        '7': '',
        '8': ''
      }
      return config[abc];
    }
  },
  {
    title: '',
    key: 'birthday',
    width: 120,
    dataIndex: 'birthday'
  },
  {
    title: '',
    key: 'address',
    width: 120,
    dataIndex: 'address'
  },
  {
    title: '',
    key: 'time',
    width: 80,
    dataIndex: 'time'
  }
]

