import React, { useEffect, useState } from 'react'
import { Card, Descriptions, Badge, Button } from 'antd'
import './user.scss';
import axios from '../../axios/axios'
import moment from 'moment';
// ,propid???
// ---->--->proper--->,proper
// url??,
//,
//,,
//--->id-->url-->
import { useParams, useHistory } from 'react-router-dom'
const Item = Descriptions.Item;
function Detail (props) {
  const [result, setResult] = useState()
  let { userId } = useParams();
  let history = useHistory();
  useEffect(() => {
    axios.axiosGet({
      url: '/table/list',
      data: {
        params: {
          userId: userId
        }
      }
    }).then((res) => {
      console.log('object :', res.result.list);
      if (res.code === '0') {
        // setResult(res.result)
        // 
        // 
        let obj = res.result.list.find(item => +item.id === +userId)
        console.log('obj', obj)
        setResult(obj)
      }
    })
  }, [userId])
  let startTime = () => {
    let a = new Date().setTime(result.start_time);
    return <>{moment(a).format('YYMMDD /  HHmmss')}</>
  }
  let endTime = () => {
    let a = new Date().setTime(result.end_time);
    return <>{moment(a).format('YYMMDD /  HHmmss')}</>
  }
  return (
    <div className="contentOrder">
      {result ? <>
        <Card title="" className='card'>
          <Button type="primary" onClick={() => { history.goBack() }}></Button>
        </Card>
        <Card title="">
          <Descriptions title='' bordered>
            <Item label='' span={12}>
              {result.username}
            </Item>
            <Item label='' span={2}>
              {result.sex === 1 ? <Badge status="processing" text="" /> : <Badge status="success" text="" />}
            </Item>
            <Item label='' span={2}>
              {result.address}
            </Item>
            <Item label='' span={2}>
              {result.total_time}
            </Item>
            <Item label='' span={2}>
              {result.status === 1 ? <Badge status="processing" text="" /> : <Badge status="success" text="" />}
            </Item>
            <Item label='' span={2}>
              {
                result.start_time ? startTime() : ''
              }
            </Item>
            <Item label='' span={2}>
              {result.start_time ? endTime() : ''}
            </Item>
            <Item label='' span={2}>
              {1000}
            </Item>
            <Item label='' span={2}>
              {300}
            </Item>
          </Descriptions>
        </Card></> : (<></>)}
    </div>
  )
}
export default Detail
