export const getOption = () => {
  let option = {
    title: {
      text: '',
      x: 'center'
    },
    legend: {
      orient: 'vertical',
      right: 10,
      top: 20,
      bottom: 20,
      data: ['', '', '', '', '', '', '']
    },
    tooltip: {
      trigger: 'item',
      formatter: "{a} <br/>{b} : {c} ({d}%)"
    },
    series: [
      {
        name: '',
        type: 'pie',
        radius: '55%',
        center: [
          '50%', '60%'
        ],
        data: [
          {
            value: 1000,
            name: ''
          },
          {
            value: 1000,
            name: ''
          },
          {
            value: 2000,
            name: ''
          },
          {
            value: 1500,
            name: ''
          },
          {
            value: 3000,
            name: ''
          },
          {
            value: 2000,
            name: ''
          },
          {
            value: 1200,
            name: ''
          },
        ],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ]
  }
  return option;
}

