export const getOption3 = () => {
  let option = {
    title: {
      text: '',
      x: 'center'
    },
    legend: {
      orient: 'vertical',
      right: 10,
      top: 20,
      bottom: 20,
      data: [
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ]
    },
    tooltip: {
      trigger: 'item',
      formatter: "{a} <br/>{b} : {c} ({d}%)"
    },
    series: [
      {
        name: '',
        type: 'pie',
        radius: '55%',
        center: [
          '50%', '50%'
        ],
        data: [
          {
            value: 1000,
            name: ''
          }, {
            value: 1000,
            name: ''
          }, {
            value: 2000,
            name: ''
          }, {
            value: 1500,
            name: ''
          }, {
            value: 3000,
            name: ''
          }, {
            value: 2000,
            name: ''
          }, {
            value: 1200,
            name: ''
          }
        ].sort(function (a, b) { return a.value - b.value; }),
        roseType: 'radius',
        animationType: 'scale',
        animationEasing: 'elasticOut',
        animationDelay: function (idx) {
          return Math.random() * 200;
        }
      }
    ]
  }
  return option;
}
