import React, { Component } from 'react'
import { Card, Button, Spin, Alert } from 'antd';
import { LoadingOutlined, PlusCircleTwoTone, SyncOutlined } from '@ant-design/icons';
import './style.scss';
export default class Loadings extends Component {
  // spin
  //  :icon+spin
  //  :spin+icon+spin
  //  +
  render () {
    const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
    const antIcon2 = <PlusCircleTwoTone twoToneColor="#52c41a" spin />
    // const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
    return (
      <>
        <Card title="Spin" className="card-wrap">
          <Spin size="small" />
          <Spin />
          <Spin size="large" />
          {/*  */}
          <Spin indicator={antIcon} />
          {/* spin */}
          <Spin size="large" indicator={antIcon2} />

          <SyncOutlined spin />
        </Card>
        <Card title="" className="card-wrap">
          <Alert
            message="React"
            description="info"
            type="info"
          ></Alert>

          <Spin>
            <Alert
              message="React"
              description="warning"
              type="warning"
            ></Alert>
          </Spin>

          <Spin tip="" indicator={antIcon2}>
            <Alert
              message="React"
              description="success"
              type="success"
            ></Alert>
          </Spin>

          <Spin>
            <Button></Button>
          </Spin>
        </Card>
      </>
    )
  }
}
