import React from 'react';
import { Select } from 'antd'
const Option = Select.Option;
export default {
  /**
   * 
   * @param {} date 
   */
  formateDate (date) {
    if (!date) return '';
    let time = new Date(date);
    let getdate;
    let month;
    parseInt(time.getMonth()) < 10 ? month = '0' + (parseInt(time.getMonth()) + 1) : month = (parseInt(time.getMonth()) + 1)
    parseInt(time.getDate()) < 10 ? getdate = '0' + time.getDate() : getdate = time.getDate();
    return time.getFullYear() + '-' +
      (month) + '-' +
      (getdate) + "   " +
      time.getHours() + ":" +
      time.getMinutes() + ":" +
      time.getSeconds();
  },
  pagination (data, callback) {
    console.log('data :', data);
    console.log('callback', callback)
    let curr = parseInt(data.result.page);
    let cur = curr;
    console.log('curr :', curr);
    console.log('cur :', cur);
    let page = {
      // 
      onChange: (current) => {
        cur = current;
        callback && callback(current)
        // ,
      },
      // 
      current: parseInt(cur),
      // 
      pageSize: data.result.page_size,
      // 
      total: data.result.item_list.length,
      // 
      showTotal: (total) => {
        console.log('total :', total);
        return `${data.result.item_list.length}`
        // return `${total}`
      },
      // 
      showQuickJumper: true
    }
    return page;

  },
  getOptionList (data) {
    if (!data) {
      return [];
    }
    let options = [] //[<Option value="0" key="all_key"></Option>];
    data.forEach((item) => {
      options.push(<Option value={item.id} key={item.id}>{item.name}</Option>)
    })
    return options;
  },


  /**
    * ETable 
    * @param {*} selectedRowKeys
    * @param {*} selectedItem
    */
  updateSelectedItem (selectedRowKeys, selectedRows, selectedIds) {
    if (selectedIds) {
      this.setState({
        selectedRowKeys,
        selectedIds: selectedIds,
        selectedItem: selectedRows
      })
    } else {
      this.setState({
        selectedRowKeys,
        selectedItem: selectedRows
      })
    }
  },
}