import utils from './../../utils/utils';
export const params = {
  page: 1
}
export const columns = [
  {
    title: 'ID',
    dataIndex: 'id'
  }, {
    title: '',
    dataIndex: 'name'
  }, {
    title: '',
    dataIndex: 'mode',
    render (mode) {
      return mode === 1 ? '' : '';
    }
  }, {
    title: '',
    dataIndex: 'op_mode',
  }, {
    title: '',
    dataIndex: 'franchisee_name'
  }, {
    title: '',
    dataIndex: 'city_admins',
    render (arr) {
      return arr.map((item) => {
        return item.user_name;
      }).join(',');
    }
  }, {
    title: '',
    dataIndex: 'open_time'
  }, {
    title: '',
    dataIndex: 'update_time',
    render: utils.formateDate //
  }, {
    title: '',
    dataIndex: 'sys_user_name'
  }
]