import React, { useRef, useEffect } from 'react'
import { Form, Select } from 'antd';
const formItemLayout = {
  labelCol: {
    span: 5
  },
  wrapperCol: { span: 19 }
}
const FormItem = Form.Item;
const Option = Select.Option;
export default function OpenCityForm (props) {
  let openCityForm = useRef(null);
  useEffect(() => {
    props.formRef(openCityForm);
  })
  return (
    <>
      <Form
        layout="horizontal"
        initialValues={{ city_id: "", op_mode: "1", mode: "" }}
        ref={openCityForm}
      >
        <FormItem label="" {...formItemLayout}
          name="city_id"
        >
          <Select style={{ width: 100 }}>
            <Option value=""></Option>
            <Option value="1"></Option>
            <Option value="2"></Option>
            <Option value="3"></Option>
          </Select>
        </FormItem>
        <FormItem label="" {...formItemLayout} name="op_mode">
          <Select style={{ width: 100 }}>
            <Option value="1"></Option>
            <Option value="2"></Option>
          </Select>
        </FormItem>
        <FormItem label="" {...formItemLayout} name="mode">
          <Select style={{ width: 100 }}>
            <Option value=""></Option>
            <Option value="1"></Option>
            <Option value="2"></Option>
          </Select>
        </FormItem>
      </Form>
    </>
  )
}
