import React, { Component } from 'react'
import { Card, Button, Radio } from 'antd';
import { PlusCircleTwoTone, LeftCircleTwoTone, RightCircleTwoTone, DownCircleTwoTone, BulbTwoTone, SearchOutlined, DeleteTwoTone, EditTwoTone } from '@ant-design/icons';
import './style.scss'
export default class Buttons extends Component {
  state = {
    loading: true
  }
  handleCloseLoading = () => {
    this.setState((prevState) => {
      return {
        loading: !prevState.loading
      }
    })
  }
  handleChange = (eve) => {
    const e = eve.target.value
    this.setState({
      size: e
    })
  }
  render () {
    return (
      <>
        <Card title="" className="card-wrap">
          <Button type="primary"></Button>
          {/* 
                           */}
          <Button></Button>
          <Button type="dashed"></Button>
          <Button type="danger"></Button>
          <Button type="link"></Button>
          <Button disabled></Button>
        </Card>
        <Card title="" className="card-wrap">
          <Button><PlusCircleTwoTone twoToneColor="#52c41a" /></Button>
          <Button><EditTwoTone twoToneColor="#52c41a" /></Button>
          <Button><DeleteTwoTone twoToneColor="#52c41a" /></Button>
          <Button shape="circle" ><SearchOutlined /></Button>
          <Button><BulbTwoTone twoToneColor="#52c41a" /></Button>
          <Button><DownCircleTwoTone twoToneColor="#52c41a" /></Button>
        </Card>
        <Card title="Loading" className="card-wrap">
          <Button type="primary" loading={this.state.loading}></Button>
          <Button type="primary" shape="circle" loading={this.state.loading} ></Button>
          <Button loading={this.state.loading} ></Button>
          <Button type="dashed" shape="circle" loading={this.state.loading} ></Button>
          <Button type="primary" onClick={this.handleCloseLoading}></Button>
        </Card>
        <Card title="" className="card-wrap">
          <Button.Group>
            <Button type="primary"  ><LeftCircleTwoTone twoToneColor="#52c41a" /></Button>
            <Button type="primary" ><RightCircleTwoTone twoToneColor="#52c41a" /></Button>
          </Button.Group>
        </Card>
        <Card title="" className="card-wrap">
          <Radio.Group value={this.state.size} onChange={this.handleChange}>
            <Radio value="small"></Radio>
            <Radio value="default"></Radio>
            <Radio value="large"></Radio>
          </Radio.Group>
          <Button type="primary" size={this.state.size}>Imooc</Button>
          {/* 
                           */}
          <Button size={this.state.size}>Imooc</Button>
          <Button type="dashed" size={this.state.size}>dashed</Button>
          <Button type="danger" size={this.state.size}>danger</Button>
        </Card>
      </>
    )
  }
}
