import React from 'react'
import { Card, Form, Input, Button, Tooltip, Select } from 'antd';
// import { UserOutlined, LockOutlined } from '@ant-design/icons';
// import { layout, tailLayout } from './sty';
const { Option } = Select;
export default function Form3 () {
  const onFinish = values => {
    console.log(values);
  };
  return (
    <>
      <Card title="" className="card-wrap">
        {/*  */}
        <Form labelCol={{ span: 9 }} wrapperCol={{ span: 12 }} name="complex-messages" onFinish={onFinish}>
          <Form.Item label="Username">
            {/* noStyle */}
            <Form.Item
              name="username"
              noStyle
              rules={[{ required: true, message: 'Username is required' }]}
            >
              <Input style={{ width: 160 }} placeholder="Please input" />
            </Form.Item>
            {/*  */}
            <Tooltip title="Useful information">
              <a href="#API" style={{ margin: '0 8px' }}>
                Need Help?
              </a>
            </Tooltip>
          </Form.Item>

          {/*  */}
          <Form.Item label="">
            <Input.Group compact>
              {/* . */}
              <Form.Item
                name={['address', '']}
                noStyle
                rules={[{ required: true, message: 'Province is required' }]}
              >
                <Select placeholder="">
                  <Option value=""></Option>
                  <Option value=""></Option>
                </Select>
              </Form.Item>
              {/* . */}
              <Form.Item
                name={['address', '']}
                noStyle
                rules={[{ required: true, message: 'Street is required' }]}
              >
                <Input style={{ width: '50%' }} placeholder="" />
              </Form.Item>
            </Input.Group>
          </Form.Item>

          {/*  */}
          <Form.Item label="BirthDate" style={{ marginBottom: 0 }}>
            {/* . */}
            <Form.Item
              name="year"
              rules={[{ required: true }]}
              style={{ display: 'inline-block', width: 'calc(50% - 8px)' }}
            >
              <Input placeholder="Input birth year" />
            </Form.Item>
            {/* .*/}
            <Form.Item
              name="month"
              rules={[{ required: true }]}
              style={{ display: 'inline-block', width: 'calc(50% - 8px)', margin: '0 8px' }}
            >
              <Input placeholder="Input birth month" />
            </Form.Item>
          </Form.Item>

          {/*  */}
          <Form.Item label=" " colon={false}>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </>
  )
}