import React, { Component } from 'react'
import { Card, Transfer, Switch } from 'antd';
const mockData = [];

for (let i = 0; i < 20; i++) {
  mockData.push({
    key: i.toString(),
    title: `${i + 1}`,
    description: `?description of content${i + 1}`,
    disabled: i % 3 < 1,
  });
}

const oriTargetKeys = mockData.filter(item => {
  // 
  // console.log('+item.key :', typeof +item.key);
  return +item.key % 3 > 1
}).map(item => item.key);

export default class trans1 extends Component {
  state = {
    targetKeys: oriTargetKeys,
    selectedKeys: [],
    disabled: false,
  };
  handleChange = (nextTargetKeys, direction, moveKeys) => {
    this.setState({ targetKeys: nextTargetKeys });

    console.log('targetKeys: ', nextTargetKeys);
    console.log('direction: ', direction);
    console.log('moveKeys: ', moveKeys);
  };

  handleSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {
    this.setState({
      // ??
      selectedKeys: [...sourceSelectedKeys, ...targetSelectedKeys]
    }, () => {
      console.log('this.state.selectedKeys', this.state.selectedKeys)
    });
    //
    console.log('sourceSelectedKeys-: ', sourceSelectedKeys);
    //
    console.log('targetSelectedKeys-: ', targetSelectedKeys);
  };

  handleScroll = (direction, e) => {
    console.log('direction:', direction);
    // ,,ul
    console.log('target:', e.target);
  };

  handleDisable = disabled => {
    this.setState({ disabled });
  };
  render () {
    const { targetKeys, selectedKeys, disabled } = this.state;
    return (
      <>
        <Card title="" className='card'>
          <Transfer
            // ,
            dataSource={mockData}
            // titles			['', '']
            titles={['Source', 'Target']}
            //  key 
            targetKeys={targetKeys}
            // 
            selectedKeys={selectedKeys}
            // 
            onChange={this.handleChange}
            // 
            onSelectChange={this.handleSelectChange}
            onScroll={this.handleScroll}
            render={item => item.title}
            disabled={disabled}
          />
          <Switch
            unCheckedChildren="disabled"
            checkedChildren="disabled"
            checked={disabled}
            onChange={this.handleDisable}
            style={{ marginTop: 16 }}
          />
        </Card>
      </>
    )
  }
}
