import React, { Component } from 'react'
import { Card, Button, Modal } from 'antd'
import './style.scss'
import '../ui.scss';
export default class Modals extends Component {
  state = {
    showModal1: false,
    showModal2: false,
    showModal3: false,
    showModal4: false
  }
  // 
  handleOpen = (Target) => {
    console.log('Target :', Target);
    this.setState({
      // 
      [Target]: true
    })
  }

  handleConfirm = (type) => {
    Modal[type]({
      title: '',
      content: '',
      onOk () {
        // alert('ok')
        console.log('ok')
      },
      onCancel () {
        alert('cancel')
      }
    })
  }
  render () {
    return (
      <>
        {/*  */}
        <Card title="" className="card-wrap">
          <Button type="primary" onClick={() => this.handleOpen('showModal1')}>Open</Button>
          <Button type="primary" onClick={() => this.handleOpen('showModal2')}></Button>
          <Button type="primary" onClick={() => this.handleOpen('showModal3')}>20px</Button>
          <Button type="primary" onClick={() => this.handleOpen('showModal4')}></Button>
        </Card>
        {/*  */}
        <Card title="" className="card-wrap">
          <Button type="primary" onClick={() => this.handleConfirm('confirm')}>Confirm</Button>
          <Button type="primary" onClick={() => this.handleConfirm('info')}>Info</Button>
          <Button type="primary" onClick={() => this.handleConfirm('success')}>Success</Button>
          <Button type="primary" onClick={() => this.handleConfirm('warning')}>warning</Button>
        </Card>
        {/* Open */}
        <Modal
          title=""
          visible={this.state.showModal1}
          onOk={() => { this.setState({ showModal1: false }); console.log('OK') }}
          onCancel={() => { this.setState({ showModal1: false }) }}
        >
          <p></p>
        </Modal>
        {/*  */}
        <Modal
          title=""
          visible={this.state.showModal2}
          okText=""
          cancelText=""
          onOk={() => { this.setState({ showModal2: false }); console.log('OK') }}
          onCancel={() => { this.setState({ showModal2: false }) }}
        >
          <p></p>
        </Modal>
        {/* 20px */}
        <Modal
          title="React"
          visible={this.state.showModal3}
          // ???
          style={{ top: '20px' }}
          onOk={() => { this.setState({ showModal3: false }); console.log('OK') }}
          onCancel={() => { this.setState({ showModal3: false }) }}
        >
          <p>React</p>
        </Modal>
        {/* ,centered */}
        <Modal
          title="React"
          visible={this.state.showModal4}
          // 
          centered
          // wrapClassName="vertical-center-modal"
          onOk={() => { this.setState({ showModal4: false }); console.log('OK') }}
          onCancel={() => { this.setState({ showModal4: false }) }}
        >
          <p>React</p>
        </Modal>
      </>
    )
  }
}
